<template>
	<el-dropdown :show-timeout="70" :hide-timeout="50" trigger="click" @command="onLanguageChange">
		<div class="lanaguage-icon">
			<el-icon size="18" color="#333">
				<icon-an-guojiyuyan />
			</el-icon>
			<span style="margin-left: 4px">
				{{ currentLanguage?.label }}
			</span>
		</div>
		<template #dropdown>
			<el-dropdown-menu>
				<el-dropdown-item v-for="item in languagesOptions" :key="item.value" :command="item.value" :disabled="currentLanguage?.value == item.value">
					{{ item.label }}
				</el-dropdown-item>
			</el-dropdown-menu>
		</template>
	</el-dropdown>
</template>

<script setup>
import { useI18n } from 'vue-i18n';
import { storage } from '@/utils/storage';
const { locale } = useI18n();

const languagesOptions = ref([
	{
		label: '',
		value: 'zh-CN',
	},
	{
		label: 'English',
		value: 'en',
	},
]);

const currentLanguage = computed(() => {
	const localeValue = locale.value || storage.get('lang') || 'zh-CN';
	return languagesOptions.value.find((v) => v.value == localeValue);
});
const onLanguageChange = (val) => {
	locale.value = val;
	// localStorage 
	storage.set('lang', val);
};
</script>
<style lang="scss" scope>
.lanaguage-icon {
	display: flex;
	align-items: center;
	color: #333;
	cursor: pointer;
}
</style>
