<template>
	<div class="header-menu" :style="{ backgroundColor: backgroundColor }">
		<div class="contenter">
			<div class="logo">
				<img width="115" height="38" :src="logo" alt="logo" />
			</div>
			<div class="menu-right">
				<ul class="menus">
					<li v-for="(item, index) in menus" :key="index" class="menu-item" @click="menuClick(item)">
						<span> {{ $t(item.label) }} </span>
					</li>
				</ul>
			</div>
			<div class="menu-left">
				<SelectLanguage />
			</div>
		</div>
	</div>
</template>

<script setup lang="ts">
import logo from '@/assets/images/logo.png';

const scrollTop = ref(0);
// 
const maxHeight = 160;

let scrolling = false;
const handleScroll = () => {
	if (!scrolling) {
		scrolling = true;
		!window.requestAnimationFrame ? setTimeout(setScrollTop, 1000 / 60) : requestAnimationFrame(setScrollTop);
	}
};

const setScrollTop = () => {
	const top = document.documentElement.scrollTop || document.body.scrollTop;
	if (top <= maxHeight) {
		scrollTop.value = top;
	} else {
		scrollTop.value = maxHeight;
	}
	scrolling = false;
};

const backgroundColor = computed(() => `rgba(256, 256, 256,${scrollTop.value / maxHeight})`);

const menus = ref([
	{ label: 'tabs.home', id: 'home' },
	{ label: 'tabs.products', id: 'product' },
	{ label: 'tabs.reviews', id: 'evaluate' },
	{ label: 'tabs.prices', id: 'price', link: '/price' },
	{ label: 'tabs.contact', id: 'connect' },
]);

const router = useRouter();
const route = useRoute();
const menuClick = (item) => {
	if (item.link) {
		if (route.fullPath == item.link) {
			const el = document.querySelector(`#${item.id}`);
			el && el.scrollIntoView({ block: 'start', behavior: 'smooth' });
		} else {
			const { href } = router.resolve({ path: item.link });
			window.open(href, '_blank');
		}
	} else if (item.id) {
		const el = document.querySelector(`#${item.id}`);
		if (el) {
			el.scrollIntoView({ block: 'start', behavior: 'smooth' });
		} else if (route.name != 'Home') {
			const { href } = router.resolve({ path: '/' });
			window.open(`${href}#/${item.id}`, '_blank');
		}
	}
};

onMounted(() => {
	if (route.hash) {
		setTimeout(() => {
			const el = document.querySelector(`${route.hash.replace('/', '')}`);
			el && el.scrollIntoView({ block: 'start', behavior: 'smooth' });
		}, 200);
	}
	// 
	window.addEventListener('scroll', handleScroll, { passive: true });
});

onUnmounted(() => {
	window.removeEventListener('scroll', handleScroll);
});
</script>

<style lang="scss" scoped>
