		height: 60px;
		padding: 0;
		margin: 0 auto;
		transition: background-color 0.3s;

		.menu-right {
			display: flex;
			flex: 1;
			align-items: center;
			justify-content: center;
			height: 60px;
			line-height: 60px;

			.menus {
				display: flex;

				.menu-item {
					width: 80px;
